
.tile {
  border: 1px solid #111;
}
.tile.empty {
    box-shadow: rgba(0, 0, 0, 0.5) 0px 5px 10px inset;
}
.tile.empty:hover {
      border-color: #111;
      cursor: default;
}
.tile.locked {
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}
.tile:hover {
    border-color: #f00;
    cursor: pointer;
}



